import { Injectable } from '@angular/core';
import { Http } from "@angular/http";
import 'rxjs';

@Injectable()
export class AppService {

	apiUrl: any = "https://jsonplaceholder.typicode.com";
	usersList: any = [];
	todoList: any = [];
  constructor(
  	private http: Http
  ) { }

  getUsers(id=null){
  	return this.http.get(this.apiUrl + "/users").map((response)=> this.usersList = response.json())
  }

  getTasks(id=null){
  	return this.http.get(this.apiUrl + '/todos?userId=' + id).map((response)=> response.json())
  }

  getAllTasks(){
	  return this.http.get(this.apiUrl + '/todos').map((response)=> this.todoList = response.json())
  }


  updateTask(id, data){
  	return this.http.put(this.apiUrl + '/todos/' + id, data).map((response)=> response.json());
  }

  addTask(task){
  	return this.http.post(this.apiUrl + '/todos', task).map((response)=> response.json());
  }

  deleteTask(id){
  	return this.http.delete(this.apiUrl + '/todos/' + id).map((response)=> response.json());
  }




}



