import { Component, OnInit, Injector, OnDestroy } from '@angular/core';
import { AppService } from '../../services/app.service';
import { BaseComponent } from "../../base/base.component";
import { ActivatedRoute } from "@angular/router";
@Component({
  selector: 'app-tasks',
  templateUrl: './tasks.component.html',
  styleUrls: ['./tasks.component.css']
})
export class TasksComponent implements OnInit {

	private params:any;
	taskList:any = []
	title: string = "";
	navParams: any;
	count:any = 200;
  constructor(
  	private appService: AppService,
  	private router: ActivatedRoute
  ) {  }

  ngOnInit() {
  	this.params = this.router.params.subscribe((value)=>{
  		this.navParams = value;
  		this.appService.getTasks(value.userId).subscribe((tasks)=> this.taskList = tasks.reverse())
  	})
  }

  onCheckboxClick(todo){
  	console.log(todo.completed);
  	this.appService.updateTask(todo.id, {completed: !todo.completed}).subscribe((v)=> console.log(v))
  }

  ngOnDestroy(){
  	this.params.unsubscribe();
  }

  onAddTodoClick(){
  	let data = {
  		userId: this.navParams.userId,
  		title: this.title,
  		completed:	false,
  		id: ++this.count
  	}
  	this.appService.addTask(data).subscribe((v)=>{
  		this.taskList.unshift(v);
  		this.title = "";
  	})
  }

  onTaskDeleteClick(todoId, index){
  	this.appService.deleteTask(todoId).subscribe((result)=>{
  		this.taskList.splice(index, 1);
  	})
  }
}



